*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;  
}

$first-color: #162E48;
$second-color: #C1E4F8;
$third-color: #FFE753;

.first-page-background{
  position: fixed;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main-section{
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  width: 100%;
  padding-top: 100px;
  padding-left: 50px;
  padding-right: 50px;
  }

.title{
  text-align: center;
  margin-bottom: 30px;
  color: #ffffff;
  line-height: 30px;
}

.firstPara{
  text-align: center;
  margin-bottom: 30px;
  color: #ffffff;
  line-height: 30px;
}

.fall-down{
  display: flex;
  text-align: center;
  align-items: center;
  width: 60px;
  height: 60px;
  border: 2px solid;
  background-color: $second-color;
  cursor: pointer;
  text-decoration: none;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  color: $first-color;
  margin-bottom: 20px;
}

.sheeps,
.cats,
.alpacas{
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  width: 100%;
  height: 100%;
  min-height: 50px;
  border: 2px solid;
  background-color: $second-color;
  cursor: pointer;
  text-decoration: none;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  color: $first-color;
  margin-bottom: 20px;
  margin: 0 2px;
  font-size: 20px;
}

.fall-down:hover,
.sheeps:hover,
.cats:hover,
.alpacas:hover{
  background-color: $third-color;
}

.fall-down:active,
.sheeps:active,
.cats:active,
.alpacas:active{
  background-color: $second-color;
}

.animal-choice{
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  min-width: 255px;
  max-width: 700px;
  height: 100px;
  display: none;
}

.audioSheep,
.audioCat,
.audioAlpaca{
  display: none;  
}
